          &nbsp;<span> > </span>&nbsp; <span class="need-ting" style="color: #555555;" >详情页</span>
        </div>
      </div>
      <div class="company-detail">
        <img :src="companyDetail.logo" alt="" class="company-logo">
        <div class="company-title">{{companyDetail.firmName}}</div>
        <div class="company-tabs">
             <ul>
               <li>
                 <div>{{companyDetail.typeEnterprise}}</div>
                 <div>企业类型</div></li>
               <li>
                 <div v-if="companyDetail">{{companyDetail.industry.split(';')[0]}}</div>
                 <div>行业</div></li>

               <li>
                 <div>{{companyDetail.registeredCapital}}万</div>
                 <div>注册资金</div></li>
               <li>
                 <div v-if="companyDetail">{{companyDetail.recordDate.substring(0,10)}}</div>
                 <div>注册时间</div></li>
               <li>
                 <div>{{companyDetail.scale}}人</div>
                 <div>从业人数</div></li>

             </ul>
        </div>
        <div class="detail-position">
          <img src="../../assets/cposition.png" alt=""  class="detail-img1">
          <span class="detai-span1">{{companyDetail.country  + companyDetail.city + companyDetail.district}}</span>
          <img src="../../assets/eye3.png" alt="" class="detail-img2">
          <span class="detai-span2">237</span>
        </div>
      </div>
      <div class="company-info">
        <div class="company-info1">企业简介</div>
        <div class="company-info2">{{companyDetail.enterpriseProfile}}</div>
        <div class="company-info1">科技成果</div>
        <div class="info-list">
          <div class="info-item " v-for="(item,key) in info" :key="key" @click="$router.push({path:'/sHome/TachieveDetails',query:{id:item.id}})" @mouseover="selectStyle (key)  "
               :class="{'active':isActive[key]}"
               @mouseout="outStyle(key)" >
            <div class="info-item1" style="position: relative">
              <div  class="tInfo-t">{{item.industry.split(',')[0]}}</div>
              {{item.resultTechnolo}}
            </div>
            <div class="info-item2">
              <ul>
                <li><span>转让金额：</span> <span style="font-size: 20px;color: #008fff;font-weight: bold;">{{item.price}}万元</span></li>
                <li><span>成果优势：</span><span>{{item.advantages}}</span><span>成果完成属性：</span><span>{{item.attribute}}</span></li>
                <li><span>成果阶段：</span><span>{{item.stage  }}</span><span>转让方式：</span><span>{{item.transferType}}</span></li>
              </ul>
            </div>
          </div>

        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "TCompanyDetail",
        data(){
           return {
               companyDetail:'',
               info:'',
               isActive:''
           }
        },
        created() {
            this.getCompanyDetail()
        },
        methods:{
            selectStyle(i){
                this.isActive[i] = true;
            },
            outStyle(i) {
                this.isActive[i] = false;
            },
            //获取企业详情信息
            getCompanyDetail(){
                var that = this;
                var id  = that.$route.query.id;
                console.log(id)
                this.$axios.get('/bussinessCenter/findBussinessCenter',{
                    params:{
                        id:id
                    }
                })
                    .then(res=>{
                        if (res.data.code==0){

                            that.companyDetail = res.data.data;
                            that.info = res.data.data.occupancyList;
                        }
                        console.log(res)
                    })
            },

        }
    }
</script>

